.ContactItem{
  width: 100%;
}
.ContactItem:not(:last-child){
  margin-bottom: 1rem;
}
.contact{
display: flex;
align-items: center;
background-color:#191D2B ;
padding: 3rem 0;
}
.right-items{
  margin-left: 2rem;
}
.right-items img{
  padding: 1rem;
  border: 1px solid #2e344e;
  margin-left: 2rem;
  width: 16%;
}
.right-items{
          
}
h6{
  font-size: 1.4rem;
  font-weight: 500;
}
.ContactPage{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin-bottom: 6rem;
}
@media screen and (max-width: 1270px){
  .ContactPage{
  grid-template-columns: repeat(1, 1fr);
  }
}
.contact-sect{
  width: 100%;
}
.map-sect{
  width: 97%;
  padding: 1rem;
  background-color:#191D2B;
}
iframe{
  width: 100%;
  height: 100%;
}
.contact-sect{
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
.title{
  margin-bottom: 5rem;
}

